import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 
const state = {
    count: 10,
    goodsID: '',
    addLevelFormVisible: false,
    level: [],
    reWithdraw: false
}

//  mutations
const mutations = {
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    },
// mmd
    // ->ID
    VIPQUERY(state, data) {
        state.goodsID = data
    },
    // ->->
    CHANGELEVEL(state, data) {
        state.addLevelFormVisible = data
    },
    // ->->->table
    INITLEVEL(state) {
        state.level = [];
    },
    // ->->->
    ADDNEWLEVEL(state, data) {
        state.level.push(data)   
    },
    // ->->->
    DELLEVEL(state, data) {
        let index = state.level.indexOf(data);
        state.level.splice(index, 1);
    },
    // 
    REGETWITHDRAWLIST(state, data) {
        state.reWithdraw = data
    }
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


