let BACK_TAG = 'vue';
//  path views,.vue
let lazy = (path) => {
    if(path.split('.').indexOf(BACK_TAG) !== -1) {
        return resolve => require([`./views${path}`], resolve)
    } else {
        return resolve => require([`./views${path}.vue`], resolve)
    }  
}

